body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.nav-item a{
  color: inherit!important;
  text-decoration: none!important;
}

.navbar-text {
  padding-right: 13px;
}

.navbar {
  padding: 0.5em 3em;
  line-height: 5em;
  max-height: 106px;
  overflow: visible;
  z-index: 9999;
}

.navbar .collapsing{
  transition: height .35s linear;
}

.navbar .show, .collapsing{
  margin-top: 7px;
  padding: 2em 2em;
  background-color: white;
  line-height: normal;
  border-top: 3px solid #0072ab;
  box-shadow: 0 2px 5px rgba(0,0,0,.1);
  z-index: 9999;
}

.navbar .show .nav-item, .navbar .collapsing .nav-item{
  padding: 1em 1em;
  border-bottom: 1px solid rgba(0,0,0,.1);
}

.border_div {
  position: absolute;
  width: 100%;
  top: 0;
  left:0;
  /* 1px off the border */
  height: 106px;
  border-bottom: 1px solid black;
  z-index: -1;
}

/* Main.js CSS */
.main {
  font-family: 'Montserrat',Helvetica,Arial,Lucida,sans-serif;
}

.main .container-fluid{
  padding-left: 0;
  padding-right: 0;
  font-weight: 700;
  font-size: 60px;
  text-shadow: 0em 0.1em 0.1em rgba(0,0,0,0.4)
}

.main .jumbotron-fluid{
  min-height: calc(100vh - 120px);
  background-position: top center;
  background-size: cover;
  background-image: url(/clone_bread_project/static/media/Homepage_Hero_Final-compressor.1d021caf.jpg);
  padding: 140px 100px;
}

